.Projects {
	padding-top: 100px;
	padding-bottom: 300px;
}

.projects_card {
	padding: 20px 35px;
	width: 65%;

	border: 1px solid white;
	border-radius: 8px;
	background-color: rgba(0, 0, 0, 0.65);
}

h2 {
	font-size: 30px !important;
}

@media only screen and (max-width: 768px) {
	.Projects {
		padding-top: 10px;
		margin-top: 0px;
	}

	.projects_card {
		width: 75%;
	}
}
