.Hero {
	padding-top: 200px;
	margin-bottom: 400px;
}

.hero_card {
	padding: 25px;
	width: fit-content;

	border: 1px solid white;
	border-radius: 8px;
	background-color: rgba(0, 0, 0, 0.65);
}

@media only screen and (max-width: 768px) {
	.Hero {
		display: flex;
		justify-content: center;
		padding-top: 40px;
		padding-bottom: 0px;
	}

	.hero_card {
		width: 85%;
	}
}
